import React  from "react"
import { Router } from "@reach/router";
import ApplicationDetails from "../templates/ApplicationDetails";

const DefaultSink = (props) => <div><pre>{JSON.stringify(props, null, 2)}</pre></div>

const App = () => {
    return (
        <Router basepath="/app">
            <ApplicationDetails path="/applications/:id" />
            <DefaultSink default />
        </Router>
    );
}

export default App;
